import axios from 'axios'
import React, { useContext, useEffect, useRef, useState } from 'react'
import { transformTextToWords } from '../../../utils'
import { useLocation } from 'react-router-dom'
import { Loader } from '../../../Components/Loader'
import UserCard from './UI/UserCard'
import { TranslationContext } from '../../../store/TranslationContext'

const SearchUsers = () => {
    const isNext = useRef(false)
    const [users, setUsers] = useState([])
    const seenUsers = useRef([])
    const [loadingUsers, setLoadingUsers] = useState(false)
    const url = new URL(window.location)
    const location = useLocation()
    const searchParameters = url.searchParams
    const searchUsers=()=>{
        setLoadingUsers(true)
        axios.post('/api/user/search-users/',
            {   search_text_words_list :  JSON.stringify(transformTextToWords(searchParameters.get('searchtext'))) ,
                search_text : searchParameters.get('searchtext'),
                seen_users : JSON.stringify(seenUsers.current)
            },
            {
                headers : {
                  "Content-Type": "multipart/form-data",
                }
        }).then((response)=>{
            setUsers((prev)=>{
                prev = [...prev, ...response.data[0]]
                seenUsers.current = prev.map(user=>user.id)
                return prev
            })
           isNext.current = response.data[1]
           setLoadingUsers(false)
        }).catch((error)=>{
            console.log(error.data)
            setLoadingUsers(false)
        })
    }
    useEffect(()=>{
        seenUsers.current = []
        setUsers([])
        searchUsers()
    }, [location])
    const {translate}= useContext(TranslationContext)
  return (
    <>
        { users.length > 0 &&
            <div className='bg-color-white'>
                { users.map(user=><div key={user.id}>
                    <UserCard user={user}/>
                </div>)}
            </div>  
        }
        {!loadingUsers && !users.length && <div className='bg-color-white py-5'><h4 className='text-center'>{translate('No user was found')}</h4></div>}
        {isNext.current && !loadingUsers && <div className='bg-color-white py-3'>
            <button disabled={loadingUsers} className={`border ${ loadingUsers ? '' : 'border-black'} mx-auto rounded-circle align-self-center p-2 aspect-ratio-1 d-flex align-items-center`} onClick={()=>{ setLoadingUsers(true); searchUsers()}}>
                <i className="fa-solid fa-plus fa-2xl"></i>
            </button>
        </div>}
        {loadingUsers && <div className='bg-color-white py-5'><Loader className='color-theme mx-auto' diam='50'/></div>}
</>
    
  )
     
  
}

export default SearchUsers